export function placeOrder(orderDetails){
    const output = fetch(order_url, {
        method:'POST',
        headers: {
                    'Accept':'application/json',
                    'Content-Type':'application/json'
                },
                body: JSON.stringify(orderDetails)
                })
    .then((res) => res.json())

    return {
        type: 'ORDER',
        payload: output
    }
}
export function signUp(signUpDetails) {
    const output = fetch(reg_url, {
        method: 'POST',
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        },
        body: JSON.stringify(signUpDetails)
        })
    .then((res) => res.json())
        
    return {
        type: 'SIGNUP',
        payload: output
    }
    
}

export function signIn(signInDetails) {
    console.log(signInDetails)
    const output = fetch(login_url,{
        method:'POST',
        headers:{
            'Accept':'application/json',
            'Content-Type':'application/json'
        },
        body: JSON.stringify(signInDetails)
        })
    .then(resp=>resp.json())
    .catch((Error) => {
        alert("Invalid UserName");
        })
    return {
        type: 'SIGNIN',
        payload: output
    }
}

export function userDetails(token) {
    const output = fetch(userinfo_url,{
        method:'GET',
        headers:{
            'x-access-token':token
        }
        })
    .then(resp=>resp.json())
    .catch((SyntaxError) => {
        alert("Invalid UserName");
        console.log(SyntaxError)
        })
    return {
        type: 'USER',
        payload: output
    }
}

export function Admin(){
    const output = fetch(order_url, {method:'GET'})
    .then((res) => res.json())

    return {
        type: 'ADMIN',
        payload: output
    }
}

export function addToCart(cartData) {
    const output = fetch(cart_url, {
        method: 'POST',
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        },
        body: JSON.stringify(cartData)
        })
    .then((res) => res.json())
        
    return {
        type: 'ADD_TO_CART',
        payload: output
    }
    
}

export function GetMyOrders(query){
    const output = fetch(`${order_url}?${query}`, 
        {method:'GET'})
    .then((res) => res.json())
    .catch((err) => console.log(err))

    return {
        type: 'USER_ORDER',
        payload: output
    }
}