import React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import propTypes from 'prop-types';
import { addToCart } from '../actions/actionfile';
import CartDisplay from '../components/CartDisplay';

class Cart extends React.Component {
    constructor() {
        super()
    }
    componentDidMount() {
        var userName = sessionStorage.getItem('userName');

        if(userName){
            const productData = JSON.parse(sessionStorage.getItem('productData'));
            const cartData = {
                "userName": sessionStorage.getItem('userName'),
                "loggedInEmail": sessionStorage.getItem('loggedInEmail'),
                "product": {
                    "title": productData.product.title,
                    "asin": productData.product.asin,
                    "image": productData.product.main_image,
                    "price": productData.product.price.current_price,
                    "rating": productData.product.reviews.rating
                }
            }

            this.props.dispatch(addToCart(cartData))
        }
    }

    userValidation = () => {
        var userName = sessionStorage.getItem('userName');

        if (userName === '' || userName === undefined || userName === null) {
            // console.log(this.state.userName)
            return(
                <div className="panel panel-primary">
                    <div className="panel-heading text-center">
                        <h3> User Sign in </h3>
                    </div>
                    <div className ="panel-body">
                        <h4 className="text-primary">You are not Signed in .Please signin</h4>
                        <Link to="/signin"><button onClick={this.selectSignIn} className="btn btn-primary">Sign In</button></Link>
                    </div>
                </div>
            )
        }
        else {
            sessionStorage.removeItem('clogin');
            <CartDisplay/>
        }
    }

    render() {
        return (
            <div className="container">
                {this.userValidation()}
            </div>
        )
    }
}

Cart.prototypes = {
    dispatch: propTypes.func
}

const mapStateToProps = (state) => {
    return {

    }
}

export default connect(mapStateToProps)(Cart);