const order = (state={},action) => {
    switch (action.type) {
        case 'ORDER':
            
            return { ...state, orderStatus: action.payload }
        
        case 'ADMIN':
            return { ...state, allOrders: action.payload }
        
        case 'USER_ORDER':
            return { ...state, myOrder: action.payload }
        
        default:
            return state
        
    
    }
}

export default order;